import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link, useLocation } from 'react-router-dom';
import './MainMenu.css';

const LippLogo = styled.img.attrs({
  src: process.env.PUBLIC_URL + 'images/menu-images/lippincott-logo.png',
  className: 'lippLogo'
})``;

const articleRoute = '/applying-the-power-of-ai-to-naming';
const ArticleMenuLink = styled(Link).attrs(props => ({
  className: 'featuredImage',
  to: props.to,
}))`
  background-image: url(${props => props.bgImage});
`;

const MainMenu = () => {
  const location = useLocation();

  const toggleNav = () => { document.body.classList.toggle('openNav'); };
  const closeNav = () => { document.body.classList.remove('openNav'); };
  const scrollToFooter = () => {
    closeNav();
    document.getElementById('footerContainer').scrollIntoView({ behavior: 'smooth' });
  };

  const scrollToTop = () => { window.scrollTo({ top: 0, behavior: 'smooth' }); };

  const getCardDetails = () => {
    if (location.pathname === articleRoute) {
      return {
        bgImage: 'images/p-block-1.webp',
        to: '/',
        title: 'Welcome to Play',
      };
    } else {
      return {
        bgImage: 'images/footer/articleCard.jpg',
        to: articleRoute,
        title: 'Applying the power of AI to naming',
      };
    }
  };
  const { to, title, bgImage } = getCardDetails();

  // Menu scroll animation on mobile ------------------------ //
  const [showMenu, setShowMenu] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (currentScrollY > lastScrollY && window.innerWidth <= 480) {
        setShowMenu(false);
      } else {
        setShowMenu(true);
      }
      setLastScrollY(currentScrollY);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [lastScrollY]);

  const menuStyle = showMenu ? {} : { transform: 'translateY(-100%)' };

  return (
    <div>
        <section className='navContainer'>
            <div className='navWrapper dFlex alignItemsCenter'>
                <div className="navigationMenu">
                    <ul>
                        <li><button onClick={scrollToFooter}>AI Experiments</button></li>
                        <li><a href="https://lippincott.com/" target="_blank" rel="noopener noreferrer">Our Work</a></li>
                        <li><a href="https://lippincott.com/about/" target="_blank" rel="noopener noreferrer">About Us</a></li>
                    </ul>
                    <p>At Lippincott, we're committed to pioneering the future of creative work. This is why we created PLAYa space where generative technology meets creative consulting, fueling innovation for our clients and ourselves. We challenge the expected, embrace the impossibly-real, and celebrate both breakthroughs and inspiring flops. Join us in this journey of continuous discovery and excitement in shaping what's next.</p>
                </div>
                <div className='menuArticle'>
                    <ArticleMenuLink to={to} bgImage={bgImage} onClick={() => {closeNav(); scrollToTop();}} />
                    <Link className='articleLink' to={to} onClick={() => {closeNav(); scrollToTop();}}>
                      {title}
                    </Link>
                </div> 
            </div>
        </section>
        <nav className='mainNav' style={menuStyle}>
            <div className='navWrapper dFlex spaceBetween alignItemsCenter'>
                <Link to="/" onClick={() => {closeNav(); scrollToTop();}}>
                    <LippLogo />
                </Link>
                <ul className='menuBtn' onClick={toggleNav}>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
            </div>
        </nav>
    </div>
  );
};

export default MainMenu;