import React from 'react';
import styled from 'styled-components';
import { Link, useLocation } from 'react-router-dom';
import './KeepExploring.css';

const articleRoute = '/applying-the-power-of-ai-to-naming';

const ExploringCard = styled(Link).attrs(props => ({
  className: 'exploringCard',
  to: props.to,
}))`
  background-image: url(${props => props.bgImage});
`;

const KeepExploring = () => {
  const location = useLocation();

  const scrollToTop = () => { window.scrollTo({ top: 0 }); };

  const getCardDetails = () => {
    if (location.pathname === articleRoute) {
      return {
        bgImage: 'images/p-block-1.webp',
        to: '/',
        title: 'Welcome to Play',
      };
    } else {
      return {
        bgImage: 'images/footer/articleCard.jpg',
        to: articleRoute,
        title: 'Applying the power of AI to naming',
      };
    }
  };

  const { to, title, bgImage } = getCardDetails();
  
  return (
      <div className='keepExploring'>
        <div className='footerWrapper'>
          <div className='dFlex flexColumn'>
            <h3 className='noe50px'>Keep exploring</h3>
            <ExploringCard to={to} bgImage={bgImage} onClick={scrollToTop}>
              <h4 className='noe60px'>{title}</h4>
            </ExploringCard>
          </div>
        </div>
      </div>
  );
};

export default KeepExploring;